.header{
    padding: 20px 50px;

    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 40px;
    z-index: 2;

    position: absolute;
    top: 0;
    left: 0;

}

.navigation ul li{
    color: white;
    cursor: pointer;
    font-weight: lighter;
    font-family: 'League Spartan', sans-serif;
    list-style: none;

    display: table-cell; 
    position: relative; 
    padding: 15px 0;
}

.navigation ul li a{
    color: #fff;
    font-size: 15px;
    text-decoration: none;
    letter-spacing: 0.15em;
    font-size: 15px;
    display: inline-block;
    padding: 5px 10px;
    position: relative;
}

.navigation ul li a::after{
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 1px;
    left: 50%;
    position: absolute;
    background: #fff;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
}

.navigation ul li a:hover:after{
    width: 100%; 
    left: 0; 
}

.navigation ul {
    display: flex;
    align-items: center;
    gap: 20px;

    padding: 0; 
    list-style: none; 
    display: table;
    text-align: center;
    
}


@media (min-width: 668px) and (max-width: 1023px){
    .header{
        padding: 15px 50px;
        gap: 50px; 
    }
    
    .navigation ul li a{
        font-size: 16px;
    }
}

@media (max-width: 668px){
    body{
        overflow-x: hidden;
    }
    .header{
        padding: 30px 10px;
        display: flex;
        justify-content: flex-end;
        align-content: center;
        gap: 40px;

        width: 100%;

        flex-direction: row-reverse;

        text-align: center;
    }
    .navigation{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 20px;
    }

    .white{
        background-color: white;
    }

    .no-color{
        background-color: transparent;
    }

    .logo{
        margin: 0 auto;
        transform: translateX(-50%);
    }
    
    .burger-menu{
        display: block !important;

        color: rgb(255, 255, 255);
        justify-self: flex-start;
        cursor: pointer;
    }

    .black-bg{
        background-color: rgba(0, 0, 0, 0.34);
        width: 100vw;
        min-height: 100vh;
        position: absolute;
        top: 0;
        left: 0;
        margin-left: 0;
    }

    .hide-bg{
        background-color: transparent;
        width: auto;
        height: auto;
        position: unset;
    }
    
    .hide-ul{
        display: none;
    }

    .show-ul{
        display: block;
    }
    .x{
        color: black;
        cursor: pointer;
    }

    .navigation ul li a{
        color: black;
    }

}