
.footer-flex{
    display: grid;
    grid-auto-columns: 1fr; 
    grid-auto-rows: 1fr; 
    grid-template-columns: 1fr 1.3fr 1fr; 
    grid-template-rows: 1fr; 
    justify-content: space-between;

    min-height: 30vh;

}

.burger-menu{
    display: none;
}

.footer-img{
    width:100%;
}

.footer-img img{
    width: 100%;
}

.footer-text{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 15px;
    margin: 0 3.823vw;
}

.footer-text h2{
    letter-spacing: 2.5px;
    text-transform: uppercase;
    color: black;
    line-height: 1.554vw;
    font-size: 1.243vw;
}

.footer-text p{
    color: hsl(0, 0%, 63%);
    font-size: 1.017vw;
    font-weight: 200;
    line-height: 1.497vw;
}

@media(max-width: 1023px){
    
.footer-flex{
    grid-template-columns: 1fr; 
    grid-template-rows: 1fr 1fr 1fr; 
    min-height: unset;
}

.footer-text{
    gap: 25px;
    
    margin: 0 3.923vw;
}

.footer-text h2{
    letter-spacing: 3.5px;
    line-height: 4.554vw;
    font-size: 4.243vw;
}

.footer-text p{
    font-size: 3.017vw;
    line-height: 4.197vw;
}
}