.main-content{
    position: relative;
    display: grid;
    grid-auto-columns: 1fr; 
    grid-auto-rows: 1fr; 
    grid-template-columns: 1.5fr 1fr; 
    grid-template-rows: 1fr; 
    justify-content: space-between;

    min-height: 70vh;
}

.main-content-texts{
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 0 10%;
    justify-content: center;
    gap: 10px;
    height: 100%;
    width: 100%;
}

.main-content-texts h1{
    font-size: 3.093vw;
    line-height: 3.198vw;
    letter-spacing: 1.5px;
    margin-bottom: 10px;
}

.main-content-texts p{
    color: hsl(0, 0%, 63%);
    font-size: 1.087vw;
    font-weight: 200;
    line-height: 1.478vw;
    margin-bottom: 15px;
    margin-right: 6px;
}
.burger-menu{
    display: none;
}

.main-content-texts button{
    background-color: transparent;
    align-self: flex-start;
    font-size: 1.221vw;
    border: none;
    cursor: pointer;
    line-height: 1.442vw;
    font-weight: 500;
    letter-spacing: 5px;
}



.main-content-texts button:hover{
    color: hsl(0, 0%, 63%);
    transition: 0.5s ease-in;
}

.main-content-img{
    width: 100%;
}

.main-content-img img{
    width: 100%;

    height: 100%;
}

.btns{
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 888888888888;
    display: flex;
    cursor: pointer;
}

.btns button{
    background-color: black;
    padding: 1.863vw;
    cursor: pointer;
    color: white;
}

.btns button:hover{
    background-color: hsl(0, 0%, 27%);
    color: white;
    transition: 0.5s ease-in-out;
}

@media(max-width: 1023px){
    .main-content{
        grid-template-columns: 1fr; 
        grid-template-rows: 1fr 1fr; 
    
        min-height: unset;
    }
    
    .main-content-texts{
        padding: 0 4%;
        gap: 20px;

    }
    
    .main-content-texts h1{
        font-size: 6.293vw;
        line-height: 7.198vw;
    }
    
    .main-content-texts p{
        font-size: 3.087vw;
        line-height: 3.978vw;
    }
    
    
    .main-content-texts button{
        font-size: 3.221vw;
        line-height: 3.442vw;
    }
    
    .btns{
        bottom: unset;
        left: unset;
        top: -15%;
        right: 0;
    }
    
    .btns button{
        padding: 2.863vw;
    }
}
